@tailwind base;
@tailwind components;
@tailwind utilities;
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800&family=Lato:wght@300;400;500;600;700;800&family=Work+Sans:wght@400;500;600;700&isplay=swap');

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

::selection {
  background-color: #535A4A;
  color: white;
}

body {
  margin: 0;
  background: #000;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  cursor: url("data:image/svg+xml,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20viewBox%3D%220%200%2025%2025%22%3E%3Ccircle%20cx%3D%2212.5%22%20cy%3D%2212.5%22%20r%3D%227%22%20fill%3D%22none%22%20stroke%3D%22%23535A4A%22%20stroke-width%3D%221.25%22%2F%3E%3C%2Fsvg%3E"), auto;
}

section {}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

img {
  pointer-events: none;
}

h1 {
  font-family: 'Averia Serif Libre', serif;
  line-height: 1.5 !important;
}

svg path {
  fill: #efefef;
  transition: 0.3s all ease-in-out;
}

svg:hover path {
  fill: #ffffff;
}

.bg-primary {
  background: #535A4A;
}

.bg-secondary {
  background: #ECE9E1;
}

.mobile-nav {
  @apply block md:hidden fixed top-10 py-2 w-full h-full bg-gray-900 duration-500;
}

#load-container {
  cursor: url("data:image/svg+xml,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20viewBox%3D%220%200%2025%2025%22%3E%3Ccircle%20cx%3D%2212.5%22%20cy%3D%2212.5%22%20r%3D%227%22%20fill%3D%22none%22%20stroke%3D%22white%22%20stroke-width%3D%221.25%22%2F%3E%3C%2Fsvg%3E"), pointer !important;
}

a {
  cursor: url("data:image/svg+xml,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20viewBox%3D%220%200%2025%2025%22%3E%3Ccircle%20cx%3D%2212.5%22%20cy%3D%2212.5%22%20r%3D%2210%22%20fill%3D%22none%22%20stroke%3D%22%23535A4A%22%20stroke-width%3D%221.25%22%2F%3E%3C%2Fsvg%3E"), pointer !important;
}

.group, #card {
  cursor: url("data:image/svg+xml,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20viewBox%3D%220%200%2025%2025%22%3E%3Ccircle%20cx%3D%2212.5%22%20cy%3D%2212.5%22%20r%3D%2210%22%20fill%3D%22none%22%20stroke%3D%22%23FF7F00%22%20stroke-width%3D%222%22%2F%3E%3C%2Fsvg%3E"), pointer !important;
}